@use 'sass:math';
/**
 * @name      math-abs
 * @namespace           sugar.scss.core.math.function
 * @type      function
 *
 * Return the absolute version of the passed number
 *
 * @param         {Number}        $number       The number to process
 * @return        {Number}                      The absolute version of the number
 *
 * @example     scss
 * Sugar.math-abs(-10); // => 10
 *
 * @since       1.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@function math-abs($number) {
  $number: test-argument('Sugar.math-abs', 'number', number, $number);
  @return math.abs($number);
}
