@use 'sass:string';
/**
 * @name      str-unquote
 * @namespace           sugar.scss.core.string.function
 * @type      function
 *
 * Return an unquoted version of the passed string
 *
 * @param         {String}        $string           The string to unquote
 * @return        {String}                          The unquoted string
 *
 * @example       scss
 * Sugar.str-unquote('hello world'); // => hello world
 *
 * @since       1.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@function str-unquote($string) {
  $string: test-argument('Sugar.str-unquote', 'string', string, $string);
  @return string.unquote($string);
}
