/**
 * @name      show-vr
 * @namespace           sugar.scss.typography.mixin
 * @type      mixin
 *
 * Display the vertical rhythme
 *
 * @param 			{Color}      [$color=#0ff] 		The color wanted
 *
 * @example     scss
 * body {
 * 		@include Sugar.show-vr();
 * }
 *
 * @since       1.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@mixin show-vr($color: #0ff) {
  $color: test-argument('Sugar.show-vr', 'color', color, $color);
  // overflow: hidden;
  position: relative;
  &:after {
    pointer-events: none;
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: -webkit-linear-gradient(
      top,
      $color 0,
      rgba(255, 255, 255, 0) 1px
    );
    background-image: -moz-linear-gradient(
      top,
      $color 0,
      rgba(255, 255, 255, 0) 1px
    );
    background-image: -o-linear-gradient(
      top,
      $color 0,
      rgba(255, 255, 255, 0) 1px
    );
    background-image: linear-gradient(
      to bottom,
      $color 0,
      rgba(255, 255, 255, 0) 1px
    );
    background-repeat: repeat-y;
    background-size: 100% 1rem;
  }
}
