/**
 * @name              helper-overflow-classes
 * @namespace         sugar.scss.helper.class
 * @type              Mixin
 *
 * This mixin generate all the "overflow" related helper classes.
 *
 * @classname     #{sel(overflow-hidden)}      Set the overflow to hidden
 * @classname     #{sel(overflow-auto)}      Set the overflow to auto
 *
 * @example         scss
 * @include Sugar.helper-overflow-classes();
 *
 * @since         2.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
@mixin helper-overflow-classes() {
  @if class-match-namespace('sugar.helper.overflow.hidden') {
    /**
    * @name 		#{sel(overflow-hidden)}
    * @namespace           sugar.scss.helper.class
    * @class-namespace     sugar.helper.overflow.hidden
    * @type        Classname
    *
    * Set the overflow to hidden
    *
    * @example 	html
    * <div class="#{sel(overflow-hidden)}" style="height:3em;">
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * </div>
    *
    * @since       2.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(overflow-hidden)} {
      overflow: hidden;
    }
  }

  @if class-match-namespace('sugar.helper.overflow.auto') {
    /**
    * @name 		#{sel(overflow-auto)}
    * @namespace           sugar.scss.helper.class
    * @class-namespace     sugar.helper.overflow.auto
    * @type        Classname
    *
    * Set the overflow to auto
    *
    * @example 	html
    * <div class="#{sel(overflow-auto)}" style="height:3em;">
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * 	<p>Hello world</p>
    * </div>
    *
    * @since       2.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(overflow-auto)} {
      overflow: auto;
    }
  }
}
