/**
 * @name      _
 * @namespace           sugar.scss.core.selector.function
 * @type    function
 *
 * Selector wrapper to be able to simply generate all of your classes with a prefix for example.
 * This mixin take his settings from @config.classes.generate.prefix
 *
 * @param 		{String} 		$selector 		The selector to process
 * @return 		{String} 						The processed selector
 *
 * @example 	scss
 * Sugar.selector('.input--default'); // => .input
 * Sugar.selector('.input-default'); // => .input
 * Sugar.selector('.input.default'); // => .input
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@function sel($selector, $prefix: setting('classes.generate.prefix')) {
  @return selector(
    $selector,
    $prefix
  );
}
