/**
 * @name        __settings
 * @type        {String}
 * @private
 *
 * Store the settings
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
 $__settings: () !default;

/**
 * @name        __current-context
 * @type        {String}
 * @private
 *
 * Store the current context
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
$__current-context: default !default;

/**
 * @name        __generated-vars
 * @type        Map
 * @private
 *
 * Store the map of the generated css vars names to avoid generating another one if already exist
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
 $__generated-vars: () !default;

/**
 * @name          __font-size
 * @type          {Number}
 * @private
 *
 * Store the current registered font size
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
$__font-size: null !default;

/**
 * @name            __svg-icons
 * @type            {Map}
 * @private
 *
 * Store all the registered svg icons
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
$__svg-icons: (
  test-user: 'data:image/svg+xml;base64,'
) !default;

/**
 * Some variables used in the math functions
 */
 $__math-pi: 3.14159265359 !default;
 $__math-default-threshold: $__math-pi/180/20 !default;
 $__math-precision: 10 !default;

/**
 * @name          __classes
 * @type          {Map}
 * @private
 *
 * Store all the classes in their namespaces
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
$__classes: () !default; // store all the classes in their namespaces

/**
 * @name          __classesFiles
 * @type          {Map}
 * @private
 *
 * Store all the classes files that have been registered to be called
 * when the ```Sugar.classes()``` mixin has been called
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
$__classesFiles: () !default;

 /**
 * @name          __classNamespacesWanted
 * @type          {List}
 * @private
 *
 * Store the list of namespaces wanted to be used across the system.
 * By default the value is the ```config.classes.generate.namespace``` setting
 * But will be overrided by the passed value to the ```Sugar.classes({namespaces})``` passed value.
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
$__classNamespacesWanted: setting('classes.generate.namespace') !default;


$classesImportString: null !default;
