/**
 * @name              helper-display-classes
 * @namespace         sugar.scss.helper.class
 * @type              Mixin
 *
 * This mixin generate all the "display" related helper classes.
 *
 * @classname     #{sel(block)}         Make an element display block
 * @classname     #{sel(inline)}         Make an element display inline
 * @classname     #{sel(inline-block)}         Make an element display inline-block
 * @classname     #{sel(show)}           Make an element visible (display block)
 * @classname     #{sel(show-inline)}           Make an element visible inline (display inline block)
 * @classname     #{sel(hidden)}           Make an element invisible (display none)
 * @classname     #{sel(hide)}           Make an element invisible (display none)
 * @classname     #{sel(visible)}           Make an element visible (visibility visible)
 * @classname     #{sel(invisible)}           Make an element invisible (visibility hidden)
 * @classname     #{sel(visually-hidden)}           Make an element visually hidden
 *
 * @example         scss
 * @include Sugar.helper-display-classes();
 *
 * @since         2.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
 */
@mixin helper-display-classes() {
  @if class-match-namespace('sugar.helper.display.block') {
    /**
    * @name 		#{sel(block)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.block
    * @type        class
    *
    * Display the element as ```block```
    *
    * @classname     #{sel(block)}         Make an element display block
    *
    * @example 	html
    * <div class="#{sel(block)}">
    * 	I'm displayed as block
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(block)} {
      display: block;
    }
  }
  @if class-match-namespace('sugar.helper.display.inline') {
    /**
    * @name 		#{sel(inline)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.inline
    * @type        class
    *
    * Display the element as ```inline```
    *
    * @classname     #{sel(inline)}         Make an element display inline
    *
    * @example 	html
    * <div class="#{sel(inline)}">
    * 	I'm displayed as inline
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(inline)} {
      display: inline;
    }
  }
  @if class-match-namespace('sugar.helper.display.inline-block') {
    /**
    * @name 		#{sel(inline-block)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.inline-block
    * @type        class
    *
    * Display the element as ```inline-block```
    *
    * @classname     #{sel(inline-block)}         Make an element display inline-block
    *
    * @example 	html
    * <div class="#{sel(inline-block)}">
    * 	I'm displayed as inline-block
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(inline-block)} {
      display: inline-block;
    }
  }
  @if class-match-namespace('sugar.helper.display.show') {
    /**
    * @name 		#{sel(show)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.show
    * @type        class
    *
    * Display the element as ```block```
    *
    * @classname     #{sel(show)}           Make an element visible (display block)
    *
    * @example 	html
    * <div class="#{sel(show)}">
    * 	I'm displayed
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(show)} {
      display: block;
    }
  }
  @if class-match-namespace('sugar.helper.display.show-inline') {
    /**
    * @name 		#{sel(show-inline)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.show-inline
    * @type        class
    *
    * Display the element as ```inline-block```
    *
    * @classname     #{sel(show-inline)}           Make an element visible inline (display inline block)
    *
    * @example 	html
    * <div class="#{sel(show-inline)}">
    * 	I'm inline displayed
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(show-inline)} {
      display: inline-block;
    }
  }
  @if class-match-namespace('sugar.helper.display.hidden') {
    /**
    * @name 		#{sel(hidden)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.hidden
    * @type        class
    *
    * Hide the element by setting the display to ```none```
    *
    * @classname     #{sel(hidden)}           Make an element invisible (display none)
    *
    * @example 	html
    * <div class="#{sel(hidden)}">
    * 	I'm hidden
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(hidden)} {
      display: none;
    }
  }
  @if class-match-namespace('sugar.helper.display.hide') {
    /**
    * @name 		#{sel(hide)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.hide
    * @type        class
    *
    * Hide the element by setting the display to ```none```
    *
    * @classname     #{sel(hide)}           Make an element invisible (display none)
    *
    * @example 	html
    * <div class="#{sel(hide)}">
    * 	I'm hidden
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(hide)} {
      display: none;
    }
  }
  @if class-match-namespace('sugar.helper.display.visible') {
    /**
    * @name 		#{sel(visible)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.visible
    * @type        class
    *
    * Display the element by setting the visibility to ```visible```
    *
    * @classname     #{sel(visible)}           Make an element visible (visibility visible)
    *
    * @example 	html
    * <div class="#{sel(visible)}">
    * 	I'm visible
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(visible)} {
      visibility: visible;
    }
  }
  @if class-match-namespace('sugar.helper.display.invisible') {
    /**
    * @name 		#{sel(invisible)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.invisible
    * @type        class
    *
    * Display the element by setting the visibility to ```hidden```
    *
    * @classname     #{sel(invisible)}           Make an element invisible (visibility hidden)
    *
    * @example 	html
    * <div class="#{sel(invisible)}">
    * 	I'm invisible
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(invisible)} {
      visibility: hidden;
    }
  }
  @if class-match-namespace('sugar.helper.display.visually-hidden') {
    /**
    * @name 		#{sel(visually-hidden)}
    * @namespace             sugar.scss.helper.class
    * @class-namespace         sugar.helper.display.visually-hidden
    * @type        class
    *
    * Hide the element from display but not for SEO.
    * This class use some tricks to make the element dissapear without using some ```display:none;```, etc...
    *
    * @classname     #{sel(visually-hidden)}           Make an element visually hidden
    *
    * @example 	html
    * <div class="#{sel(visually-hidden)}">
    * 	I'm only visually hidden
    * </div>
    *
    * @since       1.0.0
    * @author 			Olivier Bossel <olivier.bossel@gmail.com>   (https://olivierbossel.com)
    */
    .#{sel(visually-hidden)} {
      @include visually-hidden();
    }
  }
}
