/**
 * @name      math-rad
 * @namespace           sugar.scss.core.math.function
 * @type      function
 *
 * Convert the passed unitless angle to radian
 *
 * @param         {Number}        $angle      The angle to convert to radian
 * @return        {Number}                    The radian version of the angle
 *
 * @example     scss
 * Sugar.math-rad(10); // => 10rad
 *
 * @since       1.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@function math-rad($angle) {
  $angle: test-argument('Sugar.math-rad', 'angle', number, $angle);
  @return math-convert-angle($angle, rad);
}
