/**
 * @name      setup
 * @namespace           sugar.scss.core.mixin
 * @type      mixin
 *
 * Setup sugar toolkit by overriding the default [settings](../_settings.scss)
 * This has to be called **BEFORE** the [s-init mixin](./_s-init.scss)
 *
 * @param 			{Map} 			$newSettings 		The settings to override the default one with
 *
 * @example       scss
 * \@include Sugar.setup((
 * 		typo : (
 * 			font-size : 12px
 * 		),
 * 		sizes : (
 * 			small : 0.5,
 * 			big : 1.5
 * 		)
 * ));
 * // this mixin can be called as many times as you need
 * // this allows you to separate your configs setup into multiple
 * // files...
 *
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@mixin setup($newSettings) {
  $__settings: map-extend($__settings, $newSettings, true) !global;
}
