@use 'sass:meta';
/**
 * @name              map-to-css-variables
 * @namespace         sugar.scss.core.map.function
 * @type              Mixin
 *
 * THis mixin simply take a single level map and convert it to
 * css variables declarations like ```--my-cool-variable: 10px```
 *
 * @param       {Map}         $map        The map to convert to css variables declarations
 *
 * @example       scss
 * $map: (
 *   something-cool: 10px
 * );
 * @include Sugar.map-to-css-variables($map);
 *
 * @since       2.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@mixin map-to-css-variables($map) {
  :root {
    @each $key, $value in $map {
      @if $value != () {
        @include var-define($key, $value);
      }
    }
  }
}
