/**
 * @name      math-deg
 * @namespace           sugar.scss.core.math.function
 * @type      function
 *
 * Convert the passed unitless angle to degree
 *
 * @param         {Number}        $angle      The angle to convert to degree
 * @return        {Number}                    The degree version of the angle
 *
 * @example     scss
 * Sugar.math-deg(10); // => 572.9577951308deg
 *
 * @since       1.0.0
 * @author 			Olivier Bossel <olivier.bossel@gmail.com> (https://olivierbossel.com)
 */
@function math-deg($angle) {
  $angle: test-argument('Sugar.math-deg', 'angle', number, $angle);
  @return math-convert-angle($angle, deg);
}
